@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500");
@import url("https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400,700");
@import url("https://fonts.googleapis.com/css?family=Slabo+27px");
* {
  box-sizing: border-box;
}

body {
  background-color: #f5f7fb;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

.content {
  max-width: 1100px;
}

.main-index {
  background: #4687ff;
  background: -webkit-linear-gradient(135deg, #f5f7fb 0%, #f5f7fb 50%, #4687ff 50%, #4687ff 100%);
  background: -o-linear-gradient(135deg, #f5f7fb 0%, #f5f7fb 50%, #4687ff 50%, #4687ff 100%);
  background: -moz-linear-gradient(135deg, #f5f7fb 0%, #f5f7fb 50%, #4687ff 50%, #4687ff 100%);
  background: linear-gradient(135deg, #f5f7fb 0%, #f5f7fb 50%, #4687ff 50%, #4687ff 100%);
  min-height: 100vh;
  width: 100%;
  padding: 45px;
  transition: padding .25s;
}

.main-index .wrapper {
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
  background-color: #f5f7fb;
  -webkit-box-shadow: 0px 8px 35px -10px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 8px 35px -10px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 8px 35px -10px rgba(0, 0, 0, 0.5);
}

/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) and (min-width: 992px) {
  .main-index {
    padding: 35px;
  }
}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) and (min-width: 768px) {
  .main-index {
    padding: 20px;
  }
}

/* Small Devices, Tablets */
@media only screen and (max-width: 767px) and (min-width: 480px) {
  .main-index {
    padding: 10px;
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) and (min-width: 320px) {
  .main-index {
    padding: 0px;
  }
  .main-index .wrapper {
    border-radius: 0px;
  }
}

/* Custom, iPhone Retina */
@media only screen and (max-width: 320px) {
  .main-index {
    padding: 0px;
  }
  .main-index .wrapper {
    border-radius: 0px;
  }
}
